/* Style the tab */
.tab {
  float: left;
  border: 1px solid #ccc;
  background-color: #111;
  width: 15%;
  height: 650px;
}

/* Style the buttons that are used to open the tab content */
.tab button {
  display: block;
  background-color: inherit;
  color: black;
  padding: 22px 16px;
  width: 100%;
  border: none;
  outline: none;
  text-align: left;
  cursor: pointer;
  color: #818181;
  font-size: 16pt;
}

/* Change background color of buttons on hover */
.tab button:hover {
  background-color: rgb(255, 255, 255);
}

/* Create an active/current "tab button" class */
.tab button.active {
  background-color: rgb(255, 255, 255);
}

/* Style the tab content */
.tabcontent {
  float: left;
  padding: 0px 12px;
  border: 1px solid #ccc;
  width: 85%;
  border-left: none;
  height: 650px;
  display: none;
  font-size: 18pt;
}

/* Clear floats after the tab */
.clearfix::after {
  content: "";
  clear: both;
  display: table;
}
